<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-body px-lg-5 py-lg-5">
          <form role="form">
            <base-input
              placeholder="E-posta"
              addon-left-icon="ni ni-email-83"
              v-model="model.email"
            />
            <base-input
              placeholder="Parola"
              type="password"
              addon-left-icon="ni ni-lock-circle-open"
              v-model="model.password"
            />
            <div class="text-center">
			  <base-button type="primary" class="my-4" @click="handleLogin" :loading="isLoginButtonDisabled"> Giriş yap </base-button>
            </div>
		  </form>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-6">
          <router-link to="/forgetPassword" class="text-light">
            <small>Şifreni mi unuttun?</small>
          </router-link>

        </div>
        <div class="col-6 text-right">
          <router-link to="/register" class="text-light">
            <small>Kayıt ol</small>
          </router-link>
        </div>

      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";
import Vue from 'vue';

export default {
  name: "login",
  data() {
    return {
      model: {
        email: "",
        password: ""
      },
	  isLoginButtonDisabled: false,
    };
  },
  methods: {
    ...mapActions("employer", ["login"]),
    async handleLogin() {
      const { email, password } = this.model;
      if (email.length >= 6 && password.length >= 3) {
        this.isLoginButtonDisabled = true;
        await this.login({ email, password });
        this.isLoginButtonDisabled = false;
      } else {
        Vue.$toast.error('Email adresi veya parola çok kısa')
	  }
    }
  }
};
</script>
