
<template>
  <div>
    <base-header
      class="header pb-8 pt-5 pt-lg-8 d-flex align-items-center"
      style=" background-size: cover; background-position: center top;"
    >
      <!-- Mask -->
      <span class="mask bg-gradient-success opacity-8"></span>
      <!-- Header container -->
      <base-alert type="primary" v-if="!currentUser.enabled">
        İlan verebilmek için hakkımızda alanını doldurmanız ve
        profil fotoğrafınızı yüklemeniz gerekmektedir.
      </base-alert>
    </base-header>

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col-xl-4 order-xl-2 mb-5 mb-xl-0">
          <div class="card card-profile shadow">
            <div class="row justify-content-center">
              <div class="col-lg-3 order-lg-2">
                <div class="card-profile-image">
                  <img :src="currentUser.profilePicture" class="rounded-circle" />
                </div>
              </div>
            </div>
            <div class="card-header text-center border-0 pt-8 pt-md-4 pb-0 pb-md-4">
              <div class="d-flex justify-content-between">
              <input type="file" @change="onFileChanged">
              </div>
            </div>
            <div class="card-body pt-0 pt-md-4">
              <div class="row">
                <div class="col">
                  <div class="card-profile-stats d-flex justify-content-center mt-md-5">
                    <div>
                      <span class="heading">{{model.totalJobCount}}</span>
                      <span class="description">İlan sayısı</span>
                    </div>
                    <div>
                      <span class="heading">{{model.rating}}</span>
                      <span class="description">Puan</span>
                    </div>
                    <div>
                      <span class="heading">{{model.rating}}</span>
                      <span class="description">Başvuran kişi sayısı</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="text-center">
                <h3>
                  {{currentUser.organizationName}}
                  <base-button
                    size="sm"
                    rounded
                    iconOnly
                    type="success"
                    icon="ni ni-check-bold"
                    v-if="currentUser.enabled"
                    class="btn-tooltip"
                    title="Onaylı Hesap"
                    v-b-tooltip.hover.right
                  />
                  <badge type="danger" rounded v-else>Onaysız Hesap</badge>
                </h3>
                <div class="h5 font-weight-300">{{currentUser.webSite}}</div>
                <div class="h5 mt-4">{{currentUser.activityArea.name}}</div>
                <div class="h5 mt-4">{{currentUser.registerDate}} 'den beri üyesiniz.</div>
                <hr class="my-4" />
                <p>{{currentUser.about}}</p>
              </div>
            </div>
          </div>
          <br />
          <div class="card card-profile shadow">
            <div class="row justify-content-center"></div>
            <div class="card-header text-center border-0 pt-8 pt-md-4 pb-0 pb-md-4">
              <form @submit.prevent>
                <h3>Şifre Değiştir</h3>
                <div class="card-body text-center pt-0 pt-md-4"></div>

                <div class="row">
                  <div class="col-lg-6 m-auto">
                    <base-input
                      type="password"
                      label="Eski Şifre"
                      placeholder="Eski Şifre"
                      v-model="passwordModel.oldPassword"
                    />
                  </div>
                </div>
                <div class="row">
                  <div class="col-lg-6 m-auto">
                    <base-input
                      type="password"
                      label="Yeni Şifre"
                      placeholder="Yeni Şifre"
                      v-model="passwordModel.newPassword"
                    />
                  </div>
                </div>
                <div class="row">
                  <div class="col-lg-6 m-auto">
                    <base-input
                      type="password"
                      label="Yeni Şifre Tekrar"
                      placeholder="Yeni Şifre Tekrar"
                      v-model="passwordModel.newPasswordAgain"
                    />
                  </div>
                </div>
                <div class="text-center">
                  <base-button type="primary" class="my-4" @click="savePassword" :loading="isSavePasswordButtonDisabled">Değiştir</base-button>
                </div>
              </form>
            </div>
          </div>
        </div>

        <div class="col-xl-8 order-xl-1">
          <card shadow type="secondary">
            <div slot="header" class="bg-white border-0">
              <div class="row align-items-center">
                <div class="col-8">
                  <h3 class="mb-0">Hesabım</h3>
                </div>
              </div>
            </div>
            <template>
              <form @submit.prevent>
                <h6 class="heading-small text-muted mb-4">Şirket Bilgileri</h6>
                <div class="pl-lg-4">
                  <div class="row">
                    <div class="col-lg-6">
                      <base-input
                        label="Organizasyon Adı"
                        placeholder="Organizasyon Adı"
                        v-model="model.organizationName"
                      />
                    </div>
                    <div class="col-lg-6">
                      <base-input
                        disabled
                        label="E-posta"
                        placeholder="E-posta"
                        v-model="model.email"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-lg-6">
                      <p class="form-control-label">Çalışma alanı</p>
                      <base-dropdown>
                        <base-button size="xl" slot="title" class="dropdown-toggle">
                          <span v-if="!model.activity">Seçiniz</span>
                          {{this.model.activity.name}}
                        </base-button>
                        <a
                          v-for="item in activityAreas"
                          class="dropdown-item"
                          v-bind:key="item.id"
                          @click="model.activity = item"
                        >{{item.name}}</a>
                      </base-dropdown>
                    </div>
                  </div>
                </div>
                <hr class="my-4" />
                <!-- Address -->
                <h6 class="heading-small text-muted mb-4">Sosyal Medya</h6>
                <div class="pl-lg-4">
                  <div class="row">
                    <div class="col-lg-6">
                      <base-input
                        label="Web sitesi"
                        placeholder="Web sitesi"
                        v-model="model.webSite"
                      />
                    </div>
                    <div class="col-lg-6">
                      <base-input
                        label="Twitter Kullanıcı Adı"
                        placeholder="Twitter Kullanıcı Adı"
                        v-model="model.twitterUsername"
                      />
                    </div>
                  </div>
                  <div class="row">
                    <div class="col-lg-6">
                      <base-input
                        label="Facebook Kullanıcı Adı"
                        placeholder="Facebook Kullanıcı Adı"
                        v-model="model.facebookUsername"
                      />
                    </div>
                    <div class="col-lg-6">
                      <base-input
                        label="Instagram Kullanıcı Adı"
                        placeholder="Instagram Kullanıcı Adı"
                        v-model="model.instagramUsername"
                      />
                    </div>
                  </div>
                </div>
                <hr class="my-4" />
                <!-- Description -->
                <h6 class="heading-small text-muted mb-4">Hakkımızda</h6>
                <div class="pl-lg-4">
                  <div class="form-group">
                    <base-input label="Hakkımızda">
                      <textarea
                        rows="4"
                        class="form-control"
                        placeholder="Hakkımızda"
                        v-model="model.about"
                      ></textarea>
                    </base-input>
                  </div>
                </div>
              </form>
            </template>
            <div class="text-center">
              <base-button type="primary" class="my-4" @click="saveProfile" :loading="isSaveProfileButtonDisabled">Kaydet</base-button>
            </div>
          </card>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable no-unused-vars */
/* eslint-disable no-console */
import { updateProfileEmployer, getActivityArea ,updateProfilePicture } from "../api/service";
import { mapActions, mapGetters } from "vuex";
import objectToFormData from 'object-to-formdata';

export default {
  name: "user-profile",
  data() {
    return {
      currentUser: this.getEmployer(),
      model: {
        organizationName: this.$store.state.employer.currentUser
          .organizationName,
        email: this.$store.state.employer.currentUser.email,
        webSite: this.$store.state.employer.currentUser.webSite,
        twitterUsername: this.$store.state.employer.currentUser.twitterUsername,
        instagramUsername: this.$store.state.employer.currentUser
          .instagramUsername,
        facebookUsername: this.$store.state.employer.currentUser
          .facebookUsername,
        about: this.$store.state.employer.currentUser.about,
        activity: this.$store.state.employer.currentUser.activityArea,
        rating: this.$store.state.employer.currentUser.rating,
        totalJobCount: this.$store.state.employer.currentUser.totalJobCount,
        registerDate: this.$store.state.employer.currentUser.registerDate
      },
      passwordModel: {
        oldPassword: "",
        newPassword: "",
        newPasswordAgain: ""
      },
      isSaveProfileButtonDisabled: false,
      isSavePasswordButtonDisabled: false,
      activityAreas: []
    };
  },
  methods: {
    ...mapActions("employer", [
      "loginEmployer",
      "getUserInformation",
      "changePassword"
    ]),
    ...mapGetters("employer", ["getEmployer"]),
    async saveProfile() {
      this.isSaveProfileButtonDisabled = true;
      try {
		await updateProfileEmployer(this.model);
		this.getUserInformation();

      } finally {
		this.isSaveProfileButtonDisabled = false;
      }
    },
    async savePassword() {
      this.isSavePasswordButtonDisabled = true;
      try {
		const { newPassword, newPasswordAgain, oldPassword } = this.passwordModel;
		await this.changePassword({ newPassword, newPasswordAgain, oldPassword });
      } finally {
		this.isSavePasswordButtonDisabled = false;
      }
    },
    async getActivityAreaCallApi() {
      const data = await getActivityArea();
      this.activityAreas = data;
    },
    async onFileChanged (event) {
    const file = event.target.files[0];
    await updateProfilePicture(objectToFormData({ file: file }));
    this.getUserInformation();
    },
  },
  mounted() {
    this.getActivityAreaCallApi();
  }
};
</script>
