<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-body px-lg-5 py-lg-5">
          <base-input
            placeholder="Firma adı"
            addon-left-icon="ni ni-hat-3"
            v-model="model.organizationName"
          />

          <base-input
            placeholder="E-posta"
            addon-left-icon="ni ni-email-83"
            v-model="model.email"
          />

          <base-input
            placeholder="Parola"
            type="password"
            addon-left-icon="ni ni-lock-circle-open"
            v-model="model.password"
          />

          <base-input
            placeholder="Parola tekrar"
            type="password"
            addon-left-icon="ni ni-lock-circle-open"
            v-model="model.passwordAgain"
          />

          <div class="row my-4">
            <div class="col-12">
              <span>Çalışma alanı: </span>
              <base-dropdown>
                <base-button size="sm" slot="title" class="dropdown-toggle">
                  <span v-if="!model.activity">Seçiniz</span>
                  {{this.model.activity.name}}
                </base-button>
                <a
                  v-for="item in activityAreas"
                  class="dropdown-item"
                  v-bind:key="item.id"
                  @click="model.activity = item"
                >{{item.name}}</a>
              </base-dropdown>
            </div>
          </div>
          <div class="row my-4">
            <div class="col-12">
              <base-checkbox class="custom-control-alternative" v-model="privacyPolicyChecked">
                <span class="text-muted">
                  <base-button
                    size="sm"
                    @click="privacyPolicyModalShow = true"
                  >Gizlilik sözleşmesini kabul ediyorum</base-button>
                </span>
              </base-checkbox>
            </div>
            <div>
              <modal :show.sync="privacyPolicyModalShow">
                <template slot="header">
                  <h5 class="modal-title" id="exampleModalLabel">Gizlilik Sözleşmesi</h5>
                </template>
                <div>{{this.privacyPolicy}}</div>
                <template slot="footer">
                  <base-button type="secondary" @click="privacyPolicyModalShow = false">Kapat</base-button>
                </template>
              </modal>
            </div>
          </div>
          <div class="text-center">
            <base-button
              type="primary"
              class="my-4"
              @click="registerEmployer"
            >Hesap oluştur</base-button>
          </div>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-6">
          <router-link to="/forgetPassword" class="text-light">
            <small>Şifreni mi unuttun?</small>
          </router-link>
        </div>
        <div class="col-6 text-right">
          <router-link to="/login" class="text-light">
            <small>Giriş yap</small>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import { register, getActivityArea } from "../api/service";
import { privacyPolicy } from "../utils/privacyPolicy";
export default {
  name: "register",
  data() {
    return {
      model: {
        organizationName: "",
        email: "",
        password: "",
        passwordAgain: "",
        activity: ""
      },
      privacyPolicyChecked: false,
	  privacyPolicyModalShow: false,
	  privacyPolicy,
      activityAreas: []
    };
  },
  mounted() {
    this.getActivityAreaCallApi();
  },
  methods: {
    async getActivityAreaCallApi() {
      const data = await getActivityArea();
      this.activityAreas = data;
    },
    registerEmployer() {
      register(this.model);
    }
  }
};
</script>
