<template>
  <div class="row justify-content-center">
    <div class="col-lg-5 col-md-7">
      <div class="card bg-secondary shadow border-0">
        <div class="card-body px-lg-5 py-lg-5">
          <form role="form">
            <base-input
              placeholder="Kayıtlı E-postanızı giriniz"
              addon-left-icon="ni ni-email-83"
              v-model="forgetPasswordModel.forgetEmail"
              v-if="!emailCorrect"
            />
            <div class="text-center">
              <base-button type="primary" class="my-4" @click="forgetPassword" v-if="!emailCorrect" :loading="isButtonLoading">Kontrol et</base-button>
            </div>
          </form>
          <form role="form" v-if="emailCorrect">
			<base-alert type="default">
			  <strong>{{forgetPasswordModel.forgetEmail}}</strong> adresine parola sıfırlama kodu gönderildi.
			</base-alert>            <base-input
              type = "password"
              placeholder="Yeni şifrenizi giriniz"
              addon-left-icon="ni ni-lock-circle-open"
              v-model="forgetPasswordModel.newPassword"
            />
            <base-input
              placeholder="Mailinize gelen kodunuzu giriniz"
              addon-left-icon="ni ni-key-25"
              v-model="forgetPasswordModel.forgetCode"
            />
            <div class="text-center">
              <base-button type="primary" class="my-4" @click="forgetPasswordCode" v-if="emailCorrect" :loading="isButtonLoading">Şifremi değiştir</base-button>
            </div>
          </form>
        </div>
      </div>
      <div class="row mt-3">
        <div class="col-6">
           <router-link to="/login" class="text-light">
            <small>Giriş Yap</small>
          </router-link>
        </div>
        <div class="col-6 text-right">
          <router-link to="/register" class="text-light">
            <small>Kayıt ol</small>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { forgetMyPassword ,forgetMyPasswordCode} from "../api/service";
import Vue from 'vue';
import router from '../router'
export default {
  name: "forgetPassword",
  data() {
    return {
      forgetPasswordModel: {
        forgetEmail: "",
        forgetCode: "",
        newPassword: ""
      },
      emailCorrect: false,
	  isButtonLoading: false,
    };
  },
  methods: {
    async forgetPassword() {
      try {
        this.isButtonLoading = true;
      	const response = await forgetMyPassword(this.forgetPasswordModel.forgetEmail);
        if(response.status && response.status == "201"){
          Vue.$toast.success('Mail adresinize kod gönderildi! Lütfen mailinizi kontrol ediniz.', {duration: 5000});
          this.emailCorrect = true;
        }
      } finally {
        this.isButtonLoading = false;
      }
    },
    async forgetPasswordCode() {
     try {
	   this.isButtonLoading = true;
       const response = await forgetMyPasswordCode(this.forgetPasswordModel);
        if(response.status && response.status == "201"){
        Vue.$toast.success('Şifreniz başarıyla değiştirildi.', {duration: 5000});
        router.push('/login');
        }
     } finally {
	   this.isButtonLoading = false;
     }
    }

  }
};
</script>
